.header {
  background-color: #aa0601;
  color: #fff;
  max-width: auto;
  display: flex;
  justify-content: space-between;
  padding: 30px 88px; }
  .header nav ul {
    width: 492px;
    display: flex;
    justify-content: space-between;
    font-size: 20px;
    line-height: 1.45;
    margin-top: 8px; }
  .header h1 a {
    font-size: 30px;
    width: 80px;
    height: 49px;
    display: flex;
    background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/logo.png?raw=true);
    text-indent: 101%;
    overflow: hidden;
    white-space: nowrap; }
  @media (max-width: 768px) {
    .header {
      padding: 30px 39px; }
      .header nav ul {
        width: 420px; } }
  @media (max-width: 376px) {
